import React from "react";
import ModalCard from "styles/UI/ModalCard";
import styled from "styled-components";
import { useAppDispatch, useAppSelector } from "app/store/Hooks";
import { authorizedCustomAxios } from "customAxios";
import { lookUpUserProfile } from "app/store/ducks/profile/profileThunk";

const UnFollowModalInner = styled.div`
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;

    img {
        width: 90px;
        height: 90px;
        border-radius: 50%;
        margin: 30px;
    }
    h1 {
        font-size: 22px;
        line-height: 26px;
        font-weight: 300;
    }
    h4 {
        margin: 16px 30px 30px 30px;
        color: #8e8e8e;
        text-align: center;
    }

    .button-container {
        display: flex;
        flex-direction: column;
        width: 100%;

        .unfollow-button {
            color: #ed4956;
        }

        button {
            border-top: 1px solid #dbdbdb;
            font-size: 14px;
            line-height: 1.5;
            margin: 0;
            min-height: 48px;
            padding: 4px 8px;
        }
    }
`;

interface UnFollowModalProps {
    onModalOn: () => void;
    onModalOff: () => void;
    cut?: boolean; //   unfollow   false      true
}

const UnFollowModal = ({
    onModalOff,
    onModalOn,
    cut = false,
}: UnFollowModalProps) => {
    const dispatch = useAppDispatch();
    const unFollowSelectedUser = useAppSelector(
        (state) => state.profile.unFollowSelectedUser,
    );

    //   .
    const unFollowHandler = async () => {
        //  
        await authorizedCustomAxios.delete(
            `/${unFollowSelectedUser.username}/follow`,
        );

        //  
        onModalOff();

        //   (  )
        await dispatch(
            lookUpUserProfile({ username: unFollowSelectedUser.username }),
        );
    };

    //        .
    const cutFollowHandler = async () => {};

    return (
        <ModalCard
            modalType="withBackDrop"
            onModalOn={onModalOn}
            onModalOff={onModalOff}
        >
            <UnFollowModalInner>
                <img
                    src={unFollowSelectedUser.imageUrl}
                    alt={unFollowSelectedUser.username}
                />
                {cut && <h1> ?</h1>}
                {cut ? (
                    <h4>
                        {unFollowSelectedUser.username}  
                             .
                    </h4>
                ) : (
                    <h4>
                        @{unFollowSelectedUser.username} 
                        ?
                    </h4>
                )}

                <div className="button-container">
                    {cut ? (
                        <button
                            className="unfollow-button"
                            onClick={cutFollowHandler}
                        >
                            
                        </button>
                    ) : (
                        <button
                            className={"unfollow-button"}
                            onClick={unFollowHandler}
                        >
                             
                        </button>
                    )}
                    <button onClick={onModalOff}></button>
                </div>
            </UnFollowModalInner>
        </ModalCard>
    );
};

export default UnFollowModal;
