import React from "react";
import ModalCard from "styles/UI/ModalCard";
import styled from "styled-components";
import { useAppDispatch } from "app/store/Hooks";
import { logout } from "app/store/ducks/auth/authThunk";

const SettingModalInner = styled.div`
    display: flex;
    flex-direction: column;
    align-items: center;

    & > div {
        width: 100%;
        flex: 1;
        height: 48px;
        line-height: 48px;
        text-align: center;
        cursor: pointer;
    }

    & > div:not(:first-child) {
        border-top: ${(props) => props.theme.color.bd_gray} 1px solid;
    }
`;

interface SettingModalProps {
    onModalOn: () => void;
    onModalOff: () => void;
}

const SettingModal = ({ onModalOn, onModalOff }: SettingModalProps) => {
    const dispatch = useAppDispatch();
    return (
        <ModalCard
            modalType="withBackDrop"
            onModalOn={onModalOn}
            onModalOff={onModalOff}
        >
            <SettingModalInner>
                <div> </div>
                <div> </div>
                <div>  </div>
                <div></div>
                <div>  </div>
                <div> </div>
                <div>Instagram  </div>
                <div> </div>
                <div onClick={() => dispatch(logout())}></div>
                <div onClick={onModalOff}></div>
            </SettingModalInner>
        </ModalCard>
    );
};

export default SettingModal;
