import styled from "styled-components";
import ModalCard from "styles/UI/ModalCard";
import ModalHeader from "../ModalHeader";
import { ReactComponent as V } from "../../../../assets/Svgs/v.svg";
import Loading from "components/Common/Loading";

const REPORT_REASONS = [
    "",
    "    ",
    "   ",
    "   ",
    "    ",
    "  ",
    "  ",
    "  ",
    " ",
    "  ",
    " ",
    "  ",
];

const ReportModalInner = styled.div`
    & > * {
        border-bottom: ${(props) => props.theme.color.bd_gray} 1px solid;
    }
    & > h4 {
        margin-top: 24px;
        padding: 0 16px;
        padding-bottom: 16px;
        font-size: 16px;
        line-height: 24px;
        font-weight: ${(props) => props.theme.font.bold};
    }
    & > button {
        width: 100%;
        height: 50px;
        display: flex;
        align-items: center;
        padding: 16px;
        font-weight: normal;
        & > span {
            flex: 1 1 auto;
            text-align: start;
        }
        & > div {
            transform: rotate(90deg);
        }
    }
    & > .reportModal__loading {
        min-height: 122px;
        display: flex;
        align-items: center;
        justify-content: center;
    }
`;

interface ReportModalProps {
    onModalOn: () => void;
    onModalOff: () => void;
}

const ReportModal = ({ onModalOn, onModalOff }: ReportModalProps) => {
    return (
        <ModalCard
            modalType="withBackDrop"
            onModalOn={onModalOn}
            onModalOff={onModalOff}
        >
            <ReportModalInner>
                <ModalHeader title="" onModalOff={onModalOff} />
                {true ? (
                    <>
                        <h4>   </h4>
                        {REPORT_REASONS.map((reason, index) => (
                            <button key={index}>
                                <span>{reason}</span>
                                <div>
                                    <V />
                                </div>
                            </button>
                        ))}
                    </>
                ) : (
                    <div className="reportModal__loading">
                        <Loading size={32} />
                    </div>
                )}
            </ReportModalInner>
        </ModalCard>
    );
};

export default ReportModal;
