const MILLION = 1000000;
const THOUSAND: number = 1000;

const useNumberSummary = (num: number): string => {
    if (num > MILLION * 100) {
        return `${Math.floor(num / MILLION)}`; // 123
    } else if (num > MILLION) {
        return `${Math.floor((num / MILLION) * 10) / 10}`; // 1.2, 12.4
    } else if (num > THOUSAND * 100) {
        return `${Math.floor(num / THOUSAND)}`; // 123
    } else if (num > THOUSAND * 10) {
        return `${Math.floor((num / THOUSAND) * 10) / 10}`; // 12.4
    } else {
        return `${num}`; //  
    }
};
export default useNumberSummary;
