import React from "react";
import styled from "styled-components";
import { openModal } from "app/store/ducks/direct/DirectSlice";
import { useAppDispatch, useAppSelector } from "app/store/Hooks";
import CommonDirectModal from "./Modals/CommonDirectModal";

const RequestsWishContainerContainer = styled.div`
    position: absolute;
    bottom: 0;
    right: 0;
    width: 100%;

    display: flex;
    flex-direction: column;
    border-top: 1px solid #dbdbdb;

    .guide-container {
        padding: 16px;
        text-align: center;
        background-color: #fafafa;

        .wish-content {
            font-weight: 400;
            font-size: 14px;
            line-height: 18px;

            strong {
                font-weight: 600;
            }
        }

        .follow-info-content {
            color: #8e8e8e;
            font-weight: 400;
            margin-bottom: 20px;
        }

        .guide-content {
            font-weight: 400;
            color: #8e8e8e;
            font-size: 12px;
            line-height: 16px;
        }
    }

    .button-container {
        border-top: 1px solid #dbdbdb;

        height: 51px;
        display: flex;
        justify-content: space-evenly;
        align-items: center;

        .vertical-line {
            background-color: #dbdbdb;
            height: 24px;
            width: 1px;
        }

        .block-button {
            color: #ed4956;
        }

        .delete-button {
            color: #ed4956;
        }
    }
`;

const RequestsWishContainer = () => {
    const dispatch = useAppDispatch();
    const { modal } = useAppSelector((state) => state.direct);
    return (
        <RequestsWishContainerContainer>
            <div className="guide-container">
                <div className="wish-content">
                    <strong>suhw.han</strong>   
                     
                </div>
                <div className="follow-info-content">
                     120  3
                </div>
                <div className="guide-content">
                     suwa.han     
                    ?      
                        
                </div>
            </div>
            <div className="button-container">
                <button
                    className="block-button"
                    onClick={() => {
                        dispatch(openModal("block"));
                    }}
                >
                    
                </button>
                <div className="vertical-line"></div>
                <button className="delete-button"></button>
                <div className="vertical-line"></div>
                <button className="accept-button"></button>
            </div>

            {modal === "block" && (
                <CommonDirectModal
                    modalType={"block"}
                    actionName={""}
                    title={" ?"}
                    description={
                        " Instagram  ,      . Instagram      ."
                    }
                />
            )}
        </RequestsWishContainerContainer>
    );
};

export default RequestsWishContainer;
